	module.exports = {"data":{"wordpressPost":{"title":"Dry Shampoo Is The Best Invention Ever And This Is Why","content":"<p>If you haven&#8217;t already acquainted yourself with dry shampoo then it&#8217;s time you did and here&#8217;s why. The stuff is straight-up genius. Firstly, it means you can skip a wash and hair will still look bouncy and fresh.</p>\n<p>Even better, it provides excellent texture making styling more simple and for those who have limp, fine hair, it&#8217;s a godsend because it provides serious volume.</p>\n<p>But, <strong>(there&#8217;s always a but right?)</strong>, those effects are only gained when you use the stuff correctly. So here&#8217;s a quick how-to guide for you all.</p>\n<p>Segment hair into six to eight sections, depending on how much you&#8217;ve got. Hold a section in your hand and lift it at the roots. Spray a little <strong>(A LITTLE)</strong> dry shampoo at the roots and lightly massage in.</p>\n<p>Move to the next section and repeat. And that&#8217;s it! Do not spray all over your head like hair spray &#8211; it doesn&#8217;t work like that.</p>\n<p>The idea behind dry shampoo is that it absorbs oil and dirt to make hair appear clean. And where does oil and dirt tend to be? At the roots.</p>\n<p>Gradually introduce dry shampoo into your routine and you can begin to cut back on washing. This is terrific because the less washing, the less damage, says <a href=\"http://esstudio.com.au/about/\">Aleks Abadia</a> at <a href=\"http://esstudio.com.au\">Esstudio Galeria</a> and also, your colour will last longer. Bonus!</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-2978\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20193645/Cedel-TWO-143x300.jpg\" alt=\"\" width=\"143\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20193645/Cedel-TWO-143x300.jpg 143w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20193645/Cedel-TWO.jpg 319w\" sizes=\"(max-width: 143px) 100vw, 143px\" /> <img class=\"alignnone size-medium wp-image-2970\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20190703/Instant-Refresh-Dry-Shampoo-Volumising-88x300.jpg\" alt=\"\" width=\"88\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20190703/Instant-Refresh-Dry-Shampoo-Volumising-88x300.jpg 88w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20190703/Instant-Refresh-Dry-Shampoo-Volumising-768x2620.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20190703/Instant-Refresh-Dry-Shampoo-Volumising-300x1024.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20190703/Instant-Refresh-Dry-Shampoo-Volumising.jpg 1004w\" sizes=\"(max-width: 88px) 100vw, 88px\" />      <img class=\"alignnone size-medium wp-image-2971\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20190732/Pantene-Nature-Fusion-Dry-Shampoo-bd-75x300.jpg\" alt=\"\" width=\"75\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20190732/Pantene-Nature-Fusion-Dry-Shampoo-bd-75x300.jpg 75w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20190732/Pantene-Nature-Fusion-Dry-Shampoo-bd-255x1024.jpg 255w\" sizes=\"(max-width: 75px) 100vw, 75px\" />\n<p>&nbsp;</p>\n<p><strong><a href=\"https://www.priceline.com.au/cedel-dry-shampoo-for-dark-hair-387-ml\">Cedel Dry Shampoo for Dark Hair (225g), $9.80</a>.</strong> Because it leaves no powdery marks &#8211; ace for brunettes and even darker-haired women. <a href=\"https://www.priceline.com.au/tresemme-instant-refresh-volumising-dry-shampoo-250-ml\"><strong>Tresemme Instant Refresh Dry Shampoo &#8211; Volumising, $10.99</strong>.</a> Got flat, sad hair? Beef it up with this snappy spray. <a href=\"https://www.priceline.com.au/pantene-natural-fusion-dry-shampoo-original-fresh-140-g\"><strong>Pantene Pro-Vitamin Nature Fusion Original Fresh Dry Shampoo, $9.99</strong></a>. For those who like their dry shampoo with a healthy shine-inducing vitamin or three.</p>\n<img class=\"alignnone size-medium wp-image-2979\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20193735/Blush-TWO-100x300.jpg\" alt=\"\" width=\"100\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20193735/Blush-TWO-100x300.jpg 100w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20193735/Blush-TWO.jpg 226w\" sizes=\"(max-width: 100px) 100vw, 100px\" /> <img class=\"alignnone size-medium wp-image-2980\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20193802/Dove-TWO-119x300.jpg\" alt=\"\" width=\"119\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20193802/Dove-TWO-119x300.jpg 119w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/20193802/Dove-TWO.jpg 263w\" sizes=\"(max-width: 119px) 100vw, 119px\" />\n<p>&nbsp;</p>\n<p>We&#8217;re also pretty keen on <a href=\"https://www.priceline.com.au/batiste-dry-shampoo-blush-400-ml\"><strong>Batiste Dry Shampoo in Blush (400ml), $15.99</strong>.</a> Works a treat and smells heavenly. There&#8217;s a reason this brand is considered a cult classic and that&#8217;s it. <a href=\"https://www.priceline.com.au/dove-hair-therapy-refresh-care-dry-shampoo-250-ml\"><strong>Dove Refresh and Care Dry Shampoo, $9.99</strong></a>. Zaps oil like nobody&#8217;s business and, thanks to some nurturing ingredients, is gentle on locks too.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/27130814/NicM_1534_RET-proof.jpg"},"categories":[{"slug":"hair","name":"Hair"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"a2b44dd4-27be-5814-a651-91b6a850c16b"}}

